import React from "react"
import PropTypes from "prop-types"

const LayoutSection = ({ headline, children }) => (
	<section className="center" style={{ "--space": "var(--s1)" }}>
		<div className="stack" style={{ "--space": "var(--s1)" }}>
			{headline ? <h2 className="border-above">{headline}</h2> : null}
			{children}
		</div>
	</section>
)

LayoutSection.defaultProps = {
	headline: null,
}

LayoutSection.propTypes = {
	headline: PropTypes.string,
	children: PropTypes.node.isRequired,
}

export default LayoutSection
